
.menu__heading {
  margin-bottom: 32px;
  font-weight: 300;
  font-family: var(--font-family__serif);
  font-style: italic;
  text-transform: uppercase;
  color: var(--color__gray--light);
}
.menu__list {
  margin-bottom: 100px;
  color: var(--color__black);
}
.menu__times {
  margin-bottom: 50px;
  display: block;
  font-size: 18px;
  font-family: var(--font-family__sans);
  font-weight: 500;
  color: var(--color__gold);
}
.menu__item {
  margin-bottom: 32px;
}
.menu__item__heading {
  font-size: 18px;
  font-family: var(--font-family__sans);
  text-transform: uppercase;
  letter-spacing: 2px;
}
.menu__item__advisory {
  font-weight: 400;
  text-decoration: none;
  color: var(--color__gold);
}
.menu__item__price {
  margin-left: 8px;
  text-transform: lowercase;
}
.menu__item__price--sale {
  text-decoration: line-through;
}
.menu__item__desc {
  margin-top: 4px;
  font-size: 14px;
  font-family: var(--font-family__serif);
  font-weight: 300;
  font-style: italic;
  line-height: 1.6;
}



